.main {
  border: 1px solid black;
  display: flex;
  // width: 95%;
  // margin: auto;

  // .sidebar{
  //     border: 1px solid red;
  //     // width: 40%;
  //     margin: auto;
  // }

  .right {
    margin: auto;

    // border: 1px solid cyan;
    flex: 6;
    height: 100vh;

    .pathBar {
      width: 98%;
      height: 50px;
      //   border: 1px solid lime;
      margin: auto;
      margin-top: 20px;
      display: flex;
      justify-content: space-between;
      .pathBox {
        // justify-content: center;
        // text-align: center;
        width: 17%;
        height: 100%;
        // margin: auto;
        font-size: 13px;
        font-weight: bolder;

        .path {
          color: gray;
        }
      }
      .complainYear {
        height: 100%;
        width: 20%;
        font-size: 15px;
        color: blue;
        font-weight: bolder;
      }
      .selectBox {
        display: flex;
        justify-content: space-between;
        height: 100%;
        width: 25%;

        .areaFilter {
          height: 80%;
          font-size: 16px;
        }
      }
      .pathLast {
        // border: 1px solid red;
        font-size: 12px;
        color: gray;
      }
    }
    .pathBar > div {
      // border: 1px solid cyan;
      display: flex;
      align-items: center;
    }
    .pathBox > p {
      // font-weight: 800;
      // border: 1px solid red;
      color: blue;
    }

    .content {
      height: 600px;
      width: 98%;
      margin: auto;
      border: 1px solid red;

      .contentList {
        width: 100%;

        .table {
          width: 100%;
          .tableHead {
            width: 100%;
            .trHead {
              width: 100%;
              margin: auto;

              .thHead {
                border: 1px solid cyan;
                height: 40px;
                font-size: 14px;
                font-weight: bolder;
                text-transform: uppercase;
              }
              .thHead1{
                width: 13%;
                font-size: 12px;
                font-weight: 900;
              }
              .thHead2{
                width: 16%;
              }
              .thHead3{
                width: 16%;
                overflow-x: scroll;
              }
              .thHead4{
                width: 10%;
              }
              .thHead5{
                width: 10%;
              }
              .thHead6{
                width: 16%;
              }
              .thHead7{
                width: 10%;
              }
            }
          }
          .tableBody {
            .tableRow {
                .tablecol1{
                    font-size: 12px;
                    font-weight: 900;
                }
              .tablecol8 {
                // border: 1px solid red;
                justify-content: space-around;
                display: flex;

                width: 60%;
                .tablecol8Action {
                  cursor: pointer;
                }
                .tablecol8Delete {
                  cursor: pointer;
                }
              }
            }
            .tableRow > td {
              // border-top: 1px solid red;

              margin: auto;
              text-align: center;
              cursor: pointer;
            }
          }
        }
      }
    }
  }
}
